<template>
  <div>
    <b-form @submit.prevent="submit">
      <!-- çµŒè²»å¹´æœˆ -->
      <b-row>
        <b-col cols="12" xl="12" class="mb-1">
          <b-button variant="primary" class="px-16 required-btnAny">{{
            $t("REQUIREDBUTTON")
          }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel">{{ $t("EXPENSEYM") }}</label>
        </b-col>
        <b-col cols="12" xl="9">
          <b-row>
            <b-col cols="5" xl="2" class="mb-3">
              <b-form-select
                @change="changeYearMonth"
                :options="getYearOptions"
