              />
            </b-col>
            <b-col cols="5" xl="2" class="mb-3">
              <b-form-select
                @change="changeYearMonth"
                :options="getMonthOptions"
                v-model.trim="journalFile.expenseMonthFrom"
                class="optionClass dropDownButton"
              ></b-form-select>
              <show-errors
                :control="$v.journalFile.expenseMonthFrom"
                :field="$t('EXPENSEYM')"
              />
            </b-col>
            <b-col cols="1" xl="1" class="text-center mt-2">ï½ž</b-col>
            <b-col cols="5" xl="2" class="mb-3">
              <b-form-select
                @change="changeYearMonth"
                :options="getYearOptions"
                v-model.trim="journalFile.expenseYearTo"
                class="optionClass dropDownButton"
              ></b-form-select>
              <show-errors
                :control="$v.journalFile.expenseYearTo"
                :field="$t('EXPENSEYM')"
              />
            </b-col>
            <b-col cols="5" xl="2" class="mb-3">
              <b-row>
                <b-col>
                  <b-form-select
                    @change="changeYearMonth"
                    :options="getMonthOptions"
                    v-model.trim="journalFile.expenseMonthTo"
                    class="optionClass dropDownButton"
                  ></b-form-select>
                  <show-errors
                    :control="$v.journalFile.expenseMonthTo"
                    :field="$t('EXPENSEYM')"
                  />
                </b-col>
              </b-row>
            </b-col>
          </b-row>
          <p  v-if="dateErrorMessage" class="text-danger text-left">
            {{ dateErrorMessage }}
          </p>
        </b-col>
      </b-row>

      <!-- ä»•è¨³å¸³ãƒ•ã‚¡ã‚¤ãƒ« -->
      <b-row>
        <b-col cols="12" xl="12">
          <b-button variant="primary" class="px-16 required-btnAny">{{
            $t("REQUIREDBUTTON")
          }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel font-8">{{
            $t("JOURNALIMPORTFILE")
          }}</label>
        </b-col>
        <b-col cols="12" xl="10" class="mb-2">
        </b-col>
        <b-col cols="12" xl="12">
          <b-form-file
            id="avatar"
            ref="file"
            v-model="journalFile.file"
            :state="Boolean(journalFile.file)"
            :placeholder="$t('ATTACHFILE')"
            drop-placeholder="$t('ATTACHFILE')"
            data-iconName="fa fa-upload"
          ></b-form-file>
          <show-errors
            v-if="showError && fileErrorMessage == ''"
            :control="$v.journalFile.file"
            :field="$t('FILESELECTION')"
          />
          <p class="text-danger">{{ fileErrorMessage }}</p>
        </b-col>
      </b-row>

      <!-- ä¼šè¨ˆã‚½ãƒ•ãƒˆ -->

      <b-row class="mb-2">
        <b-col cols="12" xl="12">
          <b-button variant="primary" class="px-16 required-btnAny">{{
            $t("REQUIREDBUTTON")
          }}</b-button
          >&nbsp;&nbsp;
          <label class="highLightHeaderLabel">
            {{ $t("ACCOUNTINGSOFTWARE") }}
          </label>
        </b-col>
        <b-col cols="12" class="mobile-display">
          <b-row class="center-items no-gutters">
            <b-col cols="12" xl="1" class="mobile-text">
              <div class="d-flex justify-content-between">
                <div
                  v-bind:class="{ 'with-border': yayoiActive }"
                  class="radio-btn1 mx-auto text-center py-2"
                >
                  <div>
                    <b-form-radio
                      @change="yayoi"
                      v-model="journalFile.accountingSoftware"
                      name="radio-size"
                      value="01"
                    ></b-form-radio>
                  </div>
                  <div for="one">{{ $t("YAYOIACCOUNTING") }}</div>
                </div>
                <div
                v-bind:class="{ 'with-border': freeActive }"
                class="radio-btn1 mx-auto text-center py-2"
                  
                >
                  <div>
                    <b-form-radio
                      @change="free"
                      v-model="journalFile.accountingSoftware"
                      name="radio-size"
                      value="02"
                    
                    ></b-form-radio>
                  </div>
                  <div for="one">{{ $t("FREEE") }}</div>
                </div>
                <div
                v-bind:class="{ 'with-border': moneyForwardActive }"
                class="radio-btn1 mx-auto text-center py-2"
                  
                >
                  <div>
                    <b-form-radio
                     @change="moneyForward"
                      v-model="journalFile.accountingSoftware" 
                      name="radio-size"
                      value="03"
                    
                    ></b-form-radio>
                  </div>
                  <div for="one">{{ $t("MONEYFORWARD") }}</div>
                </div>
              </div>
            </b-col>
          </b-row>
        </b-col>
        <b-col id="journal-Tooltip" cols="12" xl="12" lg="12"  class="mb-2 web-display">
          <b-form-radio-group v-model="journalFile.accountingSoftware">
            <b-radio class="highLightHeaderLabel radio-group-btn" value="01">
              <b-link
                v-b-tooltip.hover.bottom.html="modeTooltipMethod1"
                href="javascript:;"
                class="link d-flex align-items-end"
              >
                {{ $t("YAYOIACCOUNTING") }}
              </b-link>
            </b-radio>

            <b-radio class="highLightHeaderLabel radio-group-btn" value="02">
              <b-link
                v-b-tooltip.hover.bottom.html="modeTooltipMethod2"
                href="javascript:;"
                class="link d-flex align-items-end"
              >
                {{ $t("FREEE") }}
              </b-link>
            </b-radio>

            <b-radio class="highLightHeaderLabel radio-group-btn" value="03">
              <b-link
                v-b-tooltip.hover.bottom.html="modeTooltipMethod3"
                href="javascrit:;"
                class="link d-flex align-items-end"
              >
                {{ $t("MONEYFORWARD") }}
              </b-link>
            </b-radio>
            <b-button
              @click="upload"
              :disabled="uploading"
              variant="primary"
              class="file-selection-btn file-btn-margin"
              >{{ $t("UPLOAD")
              }}<i v-if="uploading" class="fa fa-spinner fa-spin"
            /></b-button>
          </b-form-radio-group>
          <show-errors
            v-if="showError"
            :control="$v.journalFile.accountingSoftware"
            :field="$t('ACCOUNTINGSOFTWARE')"
          />
        </b-col>
        <b-col
          cols="12"
          class="w-100 mt-3 justify-content-center text-center mobile-display"
        >
          <b-button class="expandButton mb-2">
            <img
              src="@/assets/hatena-icon.svg"
              class="navbar-brand-full hatena-pad"
              width="10%"
              alt="Hatena-icon"
            />
            {{ $t("ACCOUNTINGSOFTWAREEXPLANATION") }}
            <span @click.prevent="showFirst = !showFirst">
              <span v-if="showFirst">
                <i
                  class="fa fa-minus mt-1 text-center float-right journal-minimizer"
                  aria-hidden="true"
                ></i>
              </span>
              <span v-if="!showFirst">
                <i
                  class="fa fa-plus mt-1 text-center float-right journal-minimizer"
                  aria-hidden="true"
                ></i>
              </span>
            </span>
          </b-button>
        </b-col>
        <b-col v-if="showFirst" cols="12" md="12">
          <ul class="list-unstyled mt-3 list-legend">
            <li class="d-flex justify-content-start mb-3">
              <div>
                <span class="block py-1 mr-1 font-weight-bold">
                  {{ $t("YAYOITITLE") }}
          </span>
                <div>{{ $t("YAYOICONTENT") }}</div>
              <ol id="modalOL">
                <li>
                  <span class="ml-2">{{ $t("YAYOIPOINTONE") }}</span>
                </li>
                <span class="ml-2">{{ $t("YAYOIPOINTONE2") }}</span>
                <li>
                  <span class="ml-2">{{ $t("YAYOIPOINTTWO") }}</span>
                </li>
                <li>
                  <span class="ml-2">{{ $t("YAYOIPOINTTHREE") }}</span>
                </li>
                <div class="ml-3">
                  {{ $t("YAYOISUBPOINTONETHREE") }}
                  <span class="ml-5 pl-1">:</span>
                  <span class="ml-1">{{ $t("YAYOISUBPOINTONEPART") }}</span>
                </div>
                <div class="ml-3">
                  {{ $t("YAYOISUBPOINTTWOTHREE") }}
                  <span class="ml-4 pl-1">:</span>
                  <span class="ml-1">{{ $t("YAYOISUBPOINTTWOPART") }}</span>
                </div>
                <li>
                  <span class="ml-2">{{ $t("YAYOIPOINTFOUR") }}</span>
                </li>
              </ol>
              </div>
            </li>
            <li class="d-flex justify-content-start mb-3">
              <div>
                <span class="block py-1 mr-1 font-weight-bold">
                  {{ $t("FREEETITLE") }}
                </span>
                <div>{{ $t("FREEECONTENT") }}</div>
              <ol id="modalOL" class="ml-2">
                <li>
                  <span class="ml-2">{{ $t("FREEEPOINTONE") }}</span>
                </li>
                <li>
                  <span class="ml-2">{{ $t("FREEEPOINTTWO") }}</span>
                </li>
                <li>
                  <span class="ml-2">{{ $t("FREEEPOINTTHREE") }}</span>
                </li>
                <li>
                  <span class="ml-2">{{ $t("FREEEPOINTFOUR") }}</span>
                </li>
              </ol>
              </div>
            </li>
            <li class="d-flex justify-content-start mb-3">
              <div>
                <span class="block py-1 mr-1 font-weight-bold">
                  {{ $t("MONEYFORWARDTITLE") }}
                </span>
                <div>{{ $t("MONEYFORWARDCONTENT") }}</div>
                <div class="ml-4">{{ $t("MONEYFORWARDTITLE") }}</div>
                <div class="ml-5">{{ $t("MONEYFORWARDPARAGRAPH") }}</div>
                <ol id="moneyForwardOL">
                  <li>
                    <span class="ml-2">{{ $t("MONEYFORWARDPOINTONE") }}</span>
                  </li>
                  <span class="ml-2 blockClass">{{
                    $t("MONEYFORWARDPOINTONE1")
                  }}</span>
                  <span class="ml-2">{{ $t("MONEYFORWARDPOINTONE2") }}</span>
                  <li>
                    <span class="ml-2">{{ $t("MONEYFORWARDPOINTTWO") }}</span>
                  </li>
               </ol>
              </div>
            </li>
          </ul>
        </b-col>

        <b-col cols="12" xl="2" lg="4" class="mobile-center mobile-display">
          <b-button
            @click="upload"
            :disabled="uploading"
            variant="primary"
            class="file-selection-btn file-btn-margin nearToButton"
            >{{ $t("UPLOAD")
            }}<i v-if="uploading" class="fa fa-spinner fa-spin"
          /></b-button>
        </b-col>
      </b-row>

      <!-- freeeé€£æº -->
      <b-row class="no-gutters">
        <b-col cols="6" xl="1">
          <label class="highLightHeaderLabel">{{
            $t("FREEECOOPERATION")
          }}</label
          >&nbsp;&nbsp;
        </b-col>

        <b-col xl="auto" class="d-flex align-items-center date-fs opacity-3">
          {{ $t("FREEEDOWNLOADTEXT") }}
        </b-col>
        <b-col cols="12" xl="12" class="mobile-center mt-2">
          <b-button
            :disabled="loading"
            @click="goToFileDownload"
            variant="primary"
            class="file-selection-btn file-btn-margin"
            >{{ $t("JOURNALDOWNLOAD") }}
            <i v-if="loading" class="fa fa-spinner fa-spin"
          /></b-button>
        </b-col>
      </b-row>
    </b-form>
    <hr class="hrBarType mb-4" />

    <b-row class="no-gutters">
      <b-col cols="12" xl="12" class="mobile-center">
        <div class="title-fw mb-3">{{ $t("JOURNALFILELIST") }}</div>
      </b-col>
      <b-col cols="12" xl="8" lg="7" class="mb-4">
          <b-form-radio-group
            id="btn-radios-2"
            v-model="selected"
            :options="options"
            button-variant="outline-primary"
            size="lg"
            name="radio-btn-outline"
            buttons
          ></b-form-radio-group> 
        <span class="web-display line-height">
          <b-link
            @click="updateFilter(1)"
            :class="{
              'active-link': tableFilter == '1',
            }"
            href="javascript:;"
            class="link small-fs border-right"
          >
            {{ $t("SHOWONLYUNIMPORTED") }}
          </b-link>
          <b-link
            @click="updateFilter(0)"
            :class="{
              'active-link': tableFilter == '0',
            }"
            href="javascript:;"
            class="link small-fs"
          >
            {{ $t("SHOWAL") }}
          </b-link>
        </span>
  
        <div class="text-center mt-2 mobile-display" cols="12">
          <b-button
            @click="updateFilter(1)"
            :class="{ mntbtn: mntBtnFlag, mnt2btn: !mntBtnFlag }"
            class="mntborder filter-btn relative"
            
            >{{ $t("SHOWONLYUNIMPORTED") }}
            <span  v-if="!mntBtnFlag2" class="tip tip-down1"></span>
          </b-button>
          <b-button
            @click="updateFilter(0)"
            :class="{ mntbtn: mntBtnFlag2, mnt2btn: !mntBtnFlag2 }"
            class="mnt3border filter-btn relative"
          
            >{{ $t("SHOWAL") }}
            <span  v-if="mntBtnFlag2" class="tip tip-down2"></span>
          </b-button>
        </div>
      </b-col>
      <b-col sm="12" xl="4" lg="5" class="text-right mb-2">
        <div class="countList text-right">
          <label>{{ $t("DISPLAYEDRESULTS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-button
              :key="index"
              v-if="!index"
              @click="perPageChange(page)"
              :class="{ mntbtn: limit == page, mnt2btn: limit != page }"
              class="mntborder"
              >{{ page }}{{ $t("COUNTS") }}</b-button
            >
            <b-button
              :key="index"
              v-else-if="index < pageOptions.length - 1"
              @click="perPageChange(page)"
              :class="{ mntbtn: limit == page, mnt2btn: limit != page }"
              class="mnt2border"
              >{{ page }}{{ $t("COUNTS") }}</b-button
            >
            <b-button
              :key="index"
              v-else
              @click="perPageChange(page)"
              :class="{ mntbtn: limit == page, mnt2btn: limit != page }"
              class="border-radius-imprv"
              >{{ page }}{{ $t("COUNTS") }}</b-button
            >
          </template>
        </div>
      </b-col>
    </b-row>
    <div id="journalTabel">
      <b-table
        ref="selectableTable"
        :select-mode="selectMode"
        :fields="fields"
        :items="tableData"
        :per-page="limit"
        :total="total"
        :startPage="start"
        @change-page="pageChange"
        :busy="isBusy"
        :borderless="false"
        :outlined="false"
        :striped="true"
        head-variant="dark"
        medium="medium"
        fixed
        sticky-header
        show-empty
      >
        <!-- ä¸€è¦§å†…å®¹ -->
        <template v-slot:cell(selectRow)="item">
          <b-form-checkbox
            :id="'checkbox-' + item.index"
            v-model="item.item.selectRow"
            :name="'checkbox-' + item.index"
            @change="toggleSelectAll(item)"
          ></b-form-checkbox>
        </template>
        <template v-slot:cell(status)="{ item }">{{ item.status }}</template>
        <template v-slot:cell(expenseYearMonth)="{ item }">{{
          item.expenseYearMonth
        }}</template>
        <template v-slot:cell(fileName)="{ item }">{{
          item.fileName
        }}</template>
        <template v-slot:cell(processingDateTime)="{ item }">{{
          item.processingDateTime
        }}</template>
        <template v-slot:cell(personInCharge)="{ item }">{{
          item.personInCharge
        }}</template>
        <template v-slot:cell(ItemConfirmation)="{ item }">
          <b-button @click="goToItemConfirmation(item)" class="cursor-pointer">
            {{ "(1)" + $t("ITEMCONFIRMATION") }}
          </b-button>
        </template>
        <template v-slot:cell(withExpenses)="{ item }">
          <b-button @click="goToCostItemBinding(item)" class="cursor-pointer">
            {{ "(2)" + $t("ITEMBINDING") }}
          </b-button>
        </template>
        <template v-slot:cell(Acquisition)="{ item }">
          <b-button
            @click="goToExtractConfirmation(item)"
            class="cursor-pointer"
          >
            {{ "(3)" + $t("EXTRACT") }}
          </b-button>
        </template>
        <template v-slot:table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
          </div>
        </template>
        <template v-slot:empty="">
          <div class="text-center text-danger">
            {{ $t("NODATAFOUND") }}
          </div>
        </template>
      </b-table>
    </div>
    <b-row>
      <b-col cols="12" xl="12">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          class="paginateProcess"
          align="center"
          size="sm"
          first-number
          last-number
          pills
        >
          <template v-slot:prev-text
            ><i class="fa fa-caret-left text-muted mr-1" aria-hidden="true"></i
            >{{ $t("PREVIOUS") }}</template
          >
          <template v-slot:next-text
            >{{ $t("NEXT")
            }}<i
              class="fa fa-caret-right text-muted ml-1"
              aria-hidden="true"
            ></i
          ></template>
          <template v-slot:page="{ page }">
            {{ page }}
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="12" class="mt-1 text-center">
        <b-button
          @click="deleteSelectedJournalFiles"
          :disabled="!checkedItems.length > 0"
          variant="primary"
          >{{ $t("CHECKEDFILEDELTEXT") }}</b-button
        >
      </b-col>
    </b-row>
  </div>
</template>

<script>
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";
export default {
  name: "JournalImport",
  components: { ShowErrors },
  data() {
    return {
      journalFile: {
        expenseYearFrom: new Date().getFullYear(),
        expenseYearTo: new Date().getFullYear(),
        expenseMonthFrom:
          new Date().getMonth() + 1 < 10
            ? "0" + (new Date().getMonth() + 1)
            : new Date().getMonth() + 1,
        expenseMonthTo:
          new Date().getMonth() + 1 < 10
            ? "0" + (new Date().getMonth() + 1)
            : new Date().getMonth() + 1,
        file: "",
        accountingSoftware: null,
      },
      getYearOptions: this.getYearList(),
      getMonthOptions: this.getMonthList(),
      selectMode: "single",
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
      checkedItems: [],
      tableData: [],
      isBusy: false,
      tableFilter: "0",
      loading: false,
      uploading: false,
      fileErrorMessage: "",
      dateErrorMessage: "",
      showError: false,
      selected_mode: "",
      journalModeOptions: [
        { text: this.$t("YAYOIACCOUNTING"), value: "01" },
        { text: this.$t("FREEE"), value: "02" },
        { text: this.$t("MONEYFORWARD"), value: "03" },
      ],
      showFirst: false,
      yayoiActive: false,
      freeActive: false,
      moneyForwardActive: false,
      mntBtnFlag: false,
      mntBtnFlag2: true,
      clientId: "",
      clientSecret: "",
    };
  },
  validations: {
    journalFile: {
      expenseYearFrom: { required },
      expenseYearTo: { required },
      expenseMonthFrom: { required },
      expenseMonthTo: { required },
      file: { required },
      accountingSoftware: { required },
    },
  },
  computed: {
    /**
     * @description mapGetters to get the data from the store
     */
    ...mapGetters({
      user_details: "getUserDetails",
    }),
    /**
     * @description fields to return number of rows
     */
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    /**
     * @description fields to return tableData
     */
    items() {
      return this.tableData;
    },
    /**
     * @description fields to return number of rows in tableData
     */
    rows() {
      return this.tableData.length;
    },
    /**
     * @description fields used in the table for keys mapping
     */
    fields() {
      return [
        {
          key: "selectRow",
          label: this.$t("SELECT"),
          class: "text-center normal-width",
        },
        {
          key: "status",
          label: this.$t("PROCESSINGSTATUS"),
          class: "text-left normal-width",
        },
        {
          key: "expenseYearMonth",
          label: this.$t("EXPENSEYM"),
          class: "text-left normal-width",
        },
        {
          key: "fileName",
          label: this.$t("FILENAME"),
          class: "text-left normal-width",
        },
        {
          key: "processingDateTime",
          label: this.$t("PROCESSINGDATE"),
          class: "text-center normal-width",
        },
        {
          key: "personInCharge",
          label: this.$t("PROCESSER"),
          class: "text-center normal-width",
        },
        {
          key: "ItemConfirmation",
          label: this.$t("ITEMCONFIRMATION"),
          class: "text-center date-normal-width",
        },
        {
          key: "withExpenses",
          label: this.$t("ITEMBINDING"),
          class: "text-center date-normal-width",
        },
        {
          key: "Acquisition",
          label: this.$t("EXTRACT"),
          class: "text-center date-normal-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
    // tableFilter(value) {
    //   // console.log(value);
    //   this.tableFilter = value;
    //   this.getJournalList();
    // },
  },
  async mounted() {
    await this.getJournalList();
  },
  created() {
    let uri = window.location.href.split("?");
    this.getClientDetails();
    //on page load check for url parameters, If url has access code, get client details and validate free account access code
    if (uri.length === 2 && localStorage.getItem("freeApiTokenFlag")) {
      this.getToken();
    } else {
      //getClient details
      this.getClientDetails(() => {});
    }
  },
  methods: {
    yayoi() {
      this.yayoiActive = true;
      this.freeActive = false;
      this.moneyForwardActive = false;
    },
    free() {
      this.yayoiActive = false;
      this.freeActive = true;
      this.moneyForwardActive = false;
    },
    moneyForward() {
      this.yayoiActive = false;
      this.freeActive = false;
      this.moneyForwardActive = true;
    },
    updateFilter(value) {
      this.mntBtnFlag = !this.mntBtnFlag;
      this.mntBtnFlag2 = !this.mntBtnFlag2;

      this.tableFilter = value;
      this.getJournalList();
    },
    modeTooltipMethod1() {
      // Note this is called each time the tooltip is first opened.
      return `<div id="journal-tooltip">`+this.$t("YAYOICONTENT")+`</div>
              <ol id="modalOL">
                <li>
                  <span class="ml-2">`+this.$t("YAYOIPOINTONE")+`</span>
                </li>
                <span class="ml-2">`+this.$t("YAYOIPOINTONE2")+`</span>
                <li>
                  <span class="ml-2">`+this.$t("YAYOIPOINTTWO")+`</span>
                </li>
                <li>
                  <span class="ml-2">`+this.$t("YAYOIPOINTTHREE")+`</span>
                </li>
                <div class="ml-3">
                  `+this.$t("YAYOISUBPOINTONETHREE")+`
                  <span class="ml-5 pl-1">:</span>
                  <span class="ml-1">`+this.$t("YAYOISUBPOINTONEPART")+`</span>
                </div>
                <div class="ml-3">
                  `+this.$t("YAYOISUBPOINTTWOTHREE")+`
                  <span class="ml-4 pl-1">:</span>
                  <span class="ml-1">`+this.$t("YAYOISUBPOINTTWOPART")+`</span>
                </div>
                <li>
                  <span class="ml-2">`+this.$t("YAYOIPOINTFOUR")+`</span>
                </li>
              </ol>`;
      // return (
      //   `<b>` +
      //   this.$t("YAYOITITLE") +
      //   `</b>
      //     <div>
      //       ` +
      //   this.$t("YAYOIBODY") +
      //   `
      //     </div>`
      // );
    },
    modeTooltipMethod2() {
      // Note this is called each time the tooltip is first opened.
      return `<div>`+this.$t("FREEECONTENT")+`</div>
              <ol id="modalOL" class="ml-2">
                <li>
                  <span class="ml-2">`+this.$t("FREEEPOINTONE")+`</span>
                </li>
                <li>
                  <span class="ml-2">`+this.$t("FREEEPOINTTWO")+`</span>
                </li>
                <li>
                  <span class="ml-2">`+this.$t("FREEEPOINTTHREE")+`</span>
                </li>
                <li>
                  <span class="ml-2">`+this.$t("FREEEPOINTFOUR")+`</span>
                </li>
              </ol>`;
      // return (
      //   `<b>` +
      //   this.$t("FREEETITLE") +
      //   `</b>
      //     <div>
      //       ` +
      //   this.$t("FREEEBODY") +
      //   `
      //     </div>`
      // );
    },
    modeTooltipMethod3() {
      // Note this is called each time the tooltip is first opened.
      return `<div><div>`+this.$t("MONEYFORWARDCONTENT")+`</div>
              <div class="ml-4">`+this.$t("MONEYFORWARDTITLE")+`</div>
              <div class="ml-5">`+this.$t("MONEYFORWARDPARAGRAPH")+`</div>
              <ol id="moneyForwardOL">
                <li>
                  <span class="ml-2">`+this.$t("MONEYFORWARDPOINTONE")+`</span>
                </li>
                <span class="ml-2 blockClass">`+this.$t("MONEYFORWARDPOINTONE1")+`</span>
                <span class="ml-2">`+this.$t("MONEYFORWARDPOINTONE2")+`</span>
                <li>
                  <span class="ml-2">`+this.$t("MONEYFORWARDPOINTTWO")+`</span>
                </li>
              </ol>
              </div>`;
      // return (
      //   `<b>` +
      //   this.$t("MONEYFORWARDTITLE2") +
      //   `</b>
      //     <div>
      //       ` +
      //   this.$t("MONEYFORWARDBODY") +
      //   `
      //     </div>`
      // );
    },
    /**
     * @description function to generate year list for dropdown
     * @name getYearList
     * @param none
     * @returns years 'list of years'
     */
    getYearList() {
      let year = new Date().getFullYear();
      year -= 11;
      let years = [];
      for (let i = 0; i <= 20; i++) {
        year++;
        years.push({ text: year + "å¹´", value: year });
      }
      return years;
    },
    /**
     * @description function to show respective journal modal
     * @name showJournalModal
     * @param none
     * @returns years 'list of years'
     */
    showJournalModal() {
      setTimeout(() => {
        switch (this.selected_mode) {
          case "01":
            this.showYayoiModal();

            break;
          case "02":
            this.showFreeeModal();

            break;
          case "03":
            this.showMoneyForwardModal();

            break;

          default:
            break;
        }
      }, 0);
    },
    /**
     * @description function to generate month list for dropdown
     * @name getMonthList
     * @param none
     * @returns months 'list of months'
     */
    getMonthList() {
      let months = [];
      for (let i = 1; i <= 12; i++) {
        if (i < 10) {
          months.push({ text: "0" + i + "æœˆ", value: "0" + i }); // if month lesser than 10 append 0 prefix to it
        } else {
          months.push({ text: i + "æœˆ", value: i });
        }
      }
      return months;
    },
    /**
     * @description function triggers onChange of file in fileupload.
     * @name fileChange
     * @param e
     * @return nothing
     */
    fileChange(e) {
      this.fileErrorMessage = "";
      var ext = event.target.files[0].name.split(".").pop();
      if (ext.toLowerCase() != "csv") {
        this.$refs.file.value = null;
        this.fileErrorMessage = this.$t("UPLOADCSVFILE");
        this.journalFile.file = "";
        return false;
      }
      this.journalFile.file = e.target.files[0].name;
    },
    /**
     * @description function to months between start and end dates
     * @name monthDiff
     * @param startDate
     * @param endDate
     * @returns months 'number of months between date'
     */
    monthDiff(startDate, endDate) {
      var months;
      months = (endDate.getFullYear() - startDate.getFullYear()) * 12;
      months -= startDate.getMonth();
      months += endDate.getMonth();
      return months;
    },
    /**
     * @description function to validate start and end dates
     * @name changeYearMonth
     * @param none
     * @returns nothing
     */
    changeYearMonth() {
      this.dateErrorMessage = "";
      let fromDate = new Date(
        this.journalFile.expenseYearFrom,
        this.journalFile.expenseMonthFrom,
        1
      );
      let toDate = new Date(
        this.journalFile.expenseYearTo,
        this.journalFile.expenseMonthTo,
        1
      );
      let months = this.monthDiff(fromDate, toDate);
      if (months < 0 || months > 12) {
        this.dateErrorMessage = this.$t("JOURNALINFODATEERROR");
      }
    },
    /**
     * @description function to upload the jounal file
     * @name upload
     * @param none
     * @returns nothing
     */
    async upload() {
      this.showError = true;
      this.dateErrorMessage = "";
      this.fileErrorMessage = "";
      this.$v.journalFile.$touch();
      if (!this.$v.journalFile.$invalid) {
        this.uploading = true;
        let fromDate = new Date(
          this.journalFile.expenseYearFrom,
          this.journalFile.expenseMonthFrom,
          1
        );
        let toDate = new Date(
          this.journalFile.expenseYearTo,
          this.journalFile.expenseMonthTo,
          1
        );
        let months = this.monthDiff(fromDate, toDate);
        console.log("months", months);
        if (months < 0 || months > 12) {
          this.dateErrorMessage = this.$t("JOURNALINFODATEERROR");
          return;
        }
        if (!this.validateLength(this.journalFile.file, 64)) {
          this.fileErrorMessage = this.$t("JOURNALFILELENGTHERROR");
          return;
        }
        let formData = new FormData();
        formData.append("client_no", this.user_details.client_account_no);
        formData.append(
          "cost_monthly_start",
          this.journalFile.expenseYearFrom +
            "" +
            this.journalFile.expenseMonthFrom
        );
        formData.append(
          "cost_monthly_end",
          this.journalFile.expenseYearTo + "" + this.journalFile.expenseMonthTo
        );
        formData.append(
          "account_soft_kbn",
          this.journalFile.accountingSoftware
        );
        formData.append("person_id", this.user_details.client_account_no);
        formData.append("create_id", this.user_details.client_account_no);
        formData.append("update_id", this.user_details.client_account_no);
        formData.append("attachment", this.$refs.file.files[0]);

        const { data } = await BasicInfoApis.postAPI(
          endPoint.JOURNALFILEINFOCREATE,
          formData
        );
        if (data.success) {
          this.$swal({
            type: "success",
            text: "",
            timer: 5000,
          });
          this.getJournalList();
          Object.assign(
            this.$data,
            (this.journalFile.expenseYearFrom = new Date().getFullYear()),
            (this.journalFile.expenseMonthFrom =
              new Date().getMonth() + 1 < 10
                ? "0" + (new Date().getMonth() + 1)
                : new Date().getMonth() + 1),
            (this.journalFile.expenseYearTo = new Date().getFullYear()),
            (this.journalFile.expenseMonthTo =
              new Date().getMonth() + 1 < 10
                ? "0" + (new Date().getMonth() + 1)
                : new Date().getMonth() + 1),
            (this.$refs.file.value = null),
            (this.journalFile.file = ""),
            (this.journalFile.accountingSoftware = null)
          );
          this.showError = false;
        } else {
          this.$swal({
            type: "warning",
            text: this.$t("INVALIDFILECONTENT"),
            timer: 5000,
          });
        }
        this.uploading = false;
      }
    },
    /**
     * @description function to get list of journal files
     * @name getJournalList
     * @param none
     * @returns nothing
     */
    async getJournalList() {
      this.$store.dispatch("updateJournalImport", {
        id: null,
      });

      this.checkedItems = [];
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.JOURNALFILEINFOREAD,
        {
          client_no: this.user_details.client_account_no,
          limit: this.limit,
          offset: this.start * this.limit,
          paginate: true,
          filter: this.tableFilter,
        }
      );
      this.total = 0;
      this.isBusy = false;
      if (data.success) {
        let responseData = data.data.journalFileInfo;
        this.total = data.data.total;
        this.tableData = [];
        // console.log("responseData", responseData);
        let rowData = [];
        responseData.forEach((item) => {
          let mapData = {
            selectRow: false,
            file_no: item.file_no,
            work_sts_kbn: Number(item.work_sts_kbn),
            status: this.getStatus(Number(item.work_sts_kbn)),
            rowCount: item.row_count ? item.row_count : "",
            expenseYearMonth:
              this.getDateMonth(item.cost_monthly_start) +
              "ï½ž" +
              this.getDateMonth(item.cost_monthly_end),
            fileName: item.file_name ? this.sliceText(item.file_name) : "",
            processingDateTime: this.tableDate(item.work_dt),
            // processingDateTime:
            //   item.work_dt == null
            //     ? ""
            //     : moment(item.work_dt)
            //         .utc(item.work_dt)
            //         .local()
            //         .format("YYYY/MM/DD HH:mm:ss"),

            personInCharge: item.client_person_name,
            ItemConfirmation: "",
            withExpenses: "",
            Acquisition: "",
          };
          rowData.push(mapData);
        });

        this.tableData = rowData;
        // console.log("tableData", this.tableData);
      } else {
        this.tableData = [];
      }
    },
    tableDate(item) {
      return (
        item.slice(0, 4) +
        this.$t("YEAR") +
        item.slice(6, 7) +
        this.$t("MONTH") +
        item.slice(8, 10) +
        this.$t("SUNDAY") +
        "  " +
        item.slice(11, 13) +
        ":" +
        item.slice(14, 16)
      );
    },
    /**
     * @description function to get the status based on the work_sts_kbn
     * @name getStatus
     * @param status
     * @returns string
     */
    getStatus(status) {
      switch (status) {
        case 0:
          return this.$t("UNTREATED");
        case 1:
          return this.$t("PARTIALCOSTTIED");
        case 2:
          return this.$t("EXPENSETIED");
        case 3:
          return this.$t("PARTIALLYIMPORTED");
        case 4:
          return this.$t("IMPORTED");
      }
    },
    sliceText(text) {
      if (text.length >= 12) {
        return text.slice(0, 8) + "...csv";
      }
      return text;
    },
    /**
     * @description function to raise an event onRowSelected
     * @name onRowSelected
     * @param items
     * @returns nothing
     */
    onRowSelected(items) {
      this.$emit("send_selected_data", items);
      this.selected = items;
    },
    /**
     * @description function to show yayoi modal popup
     * @name showYayoiModal
     * @param none
     * @returns nothing
     */
    showYayoiModal() {
      this.$refs["yayoi-modal"].show();
    },
    /**
     * @description function to show freee modal popup
     * @name showFreeeModal
     * @param none
     * @returns nothing
     */
    showFreeeModal() {
      this.$refs["freee-modal"].show();
    },
    /**
     * @description function to show money forward modal popup
     * @name showMoneyForwardModal
     * @param none
     * @returns nothing
     */
    showMoneyForwardModal() {
      this.$refs["moneyForward-modal"].show();
    },
    /**
     * @description function to show money forward modal popup
     * @name onModeHover
     * @param none
     */
    onModeHover(event) {
      setTimeout(() => {
        console.log(
          "event",
          event,
          event.target,
          document.getElementById(event.target.id)
        );
        this.$root.$emit("bv::enable::tooltip", "tooltip-button-disable");
      }, 0);
    },
    /**
     * @description function to trigger toggle all select checkbox in the table
     * @name toggleSelectAll
     * @param item
     * @returns nothing
     */
    toggleSelectAll(item) {
      item.item.selectRow = !item.item.selectRow;
      this.checkedItems = this.tableData.filter((checkedItem) => {
        if (checkedItem.selectRow == true) {
          return checkedItem;
        }
      });
    },
    /**
     * @description function to get the rows count
     * @name getRowCount
     * @param none
     * @returns number
     */
    getRowCount() {
      return this.items.length;
    },
    /**
     * @description function which triggers on page change in the table
     * @name pageChange
     * @param page
     * @returns nothing
     */
    pageChange(page) {
      this.start = page - 1;
      this.getJournalList();
    },
    /**
     * @description function which triggers on page change in the table
     * @name perPageChange
     * @param page
     * @returns nothing
     */
    perPageChange(page) {
      this.limit = page;
      this.getJournalList();
    },
    /**
     * @description function to update the file_no to store and navigate to confirmation screen
     * @name goToItemConfirmation
     * @param item
     * @returns nothing
     */
    goToItemConfirmation(item) {
      this.$store.dispatch("updateJournalImport", {
        id: item.file_no,
        rowCount: item.rowCount ? item.rowCount : "",
      });
      this.$router.push("/journal/import/item/confirmation");
    },
    /**
     * @description function to update the file_no to store and navigate to cost item binding screen
     * @name goToCostItemBinding
     * @param item
     * @returns nothing
     */
    goToCostItemBinding(item) {
      this.$store.dispatch("updateJournalImport", {
        id: item.file_no,
        rowCount: item.rowCount ? item.rowCount : "",
      });
      this.$router.push({ name: "cost_item_binding" });
    },
    /**
     * @description function to update the file_no to store and navigate to confirmation screen
     * @name goToExtractConfirmation
     * @param item
     * @returns nothing
     */
    goToExtractConfirmation(item) {
      this.$store.dispatch("updateJournalImport", {
        id: item.file_no,
        rowCount: item.rowCount ? item.rowCount : "",
      });
      this.$router.push({ name: "extract_confirmation" });
    },
    /**
     * @description function to refresh the token and navigate to the download screen
     * @name goToFileDownload
     * @param none
     * @returns nothing
     */
    async goToFileDownload() {
      /**To do Access Token Validation */
      let uri = window.location.href.split("?");
      this.loading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.INTEGRATIONREFRESHTOKEN,
        {
          client_no: this.user_details.client_account_no,
          create_id: this.user_details.client_account_no,
          update_id: this.user_details.client_account_no,
          redirect_uri: uri[0],
        }
      );
      this.loading = false;
      // console.log("!!!!!!!!!!!!", data);
      if (data.success) {
        localStorage.setItem("accessToken", data.data.access_token);
        this.$store.dispatch("updateLinkageInfoNo", {
          linkageInfoNo: data.data.linkage_info_no,
        });
        this.$router.push({ name: "file_download" });
      } else {
        // this.$router.push({
        //   name: "freeAPI_accesstoken_fetch",
        // });
        this.getToken();
      }
    },
    /**
     * @description function to get client details by passing client account number
     * @name getClientDetails
     * @param callback function
     * @returns nothing
     */
    async getClientDetails() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.INTEGRATIONCLIENTDETAILS,
        {
          client_no: localStorage.getItem("client_account_no"),
        }
      );
      // if success flag is true disable clientId and clientSecret text input
      if (data.success) {
        this.clientId = data.data.client_id;
        this.clientSecret = data.data.client_secret;
      }
    },
    /**
     * @description function to get the token from the current uri
     * @name getToken
     * @param none
     * @returns nothing
     */
    async getToken() {
      console.log(this.clientId, "llllllllllllll");
      let uri = window.location.href.split("?");
      //get for url parameter, check for url parameter has free account code
      if (uri.length === 2) {
        let uriParams = uri[1].split("&");
        let getUriParams = {};
        let tmpArray = "";
        uriParams.forEach(function (data) {
          tmpArray = data.split("=");
          if (tmpArray.length == 2) getUriParams[tmpArray[0]] = tmpArray[1];
        });
        let code = "";
        if (getUriParams.code[getUriParams.code.length - 1] === "#") {
          code = getUriParams.code.slice(0, -1);
        } else {
          code = getUriParams.code;
        }
        console.log(this.clientId);
        //validate and add access code
        const { data } = await BasicInfoApis.postAPI(
          endPoint.INTEGRATIONREADACCESSTOKEN,
          {
            code: code,
            client_no: localStorage.getItem("client_account_no"),
            create_id: localStorage.getItem("client_account_no"),
            update_id: localStorage.getItem("client_account_no"),
            redirect_uri: uri[0],
            client_id: this.clientId,
            client_secret: this.clientSecret,
          }
        );
        if (data.message.code !== 2005) {
          //if code is not correct navigate to free account
          localStorage.setItem("freeApiTokenFlag", true);
          window.open(
            `https://secure.freee.co.jp/oauth/authorize?client_id=${
              this.clientId
            }&redirect_uri=${encodeURIComponent(uri[0])}&response_type=code#`
          );
        } else {
          //if the code is validated navigate to crf-54
          localStorage.removeItem("freeApiTokenFlag");
          localStorage.removeItem("clientId");
          localStorage.removeItem("clientSecret");
          this.$store.dispatch("updateLinkageInfoNo", {
            linkageInfoNo: data.data.linkage_info_no,
          });
          this.$router.push({ name: "file_download" });
        }
      } else {
        //if url has access code, Then navigate to free account to get code
        localStorage.setItem("freeApiTokenFlag", true);
        window.open(
          `https://secure.freee.co.jp/oauth/authorize/?client_id=${
            this.clientId
          }&redirect_uri=${encodeURIComponent(uri[0])}&response_type=code#`
        );
      }
    },
    /**
     * @description function to delete the selected jounal file from the table
     * @name deleteSelectedJournalFiles
     * @param none
     * @returns nothing
     */
    async deleteSelectedJournalFiles() {
      this.isBusy = true;
      let iterateConditions = [
        { key: "file_no", value: this._.map(this.checkedItems, "file_no") },
      ];
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERSOFTDETJOURNALFILEINFO,
        {
          conditions: iterateConditions,
        }
      );

      await BasicInfoApis.postAPI(
        "master/softdelete/t_journal_expense_item_link_info",
        {
          conditions: iterateConditions,
          update_id: this.user_details.client_account_no,
        }
      );
      await BasicInfoApis.postAPI("master/softdelete/t_journal_data_info", {
        conditions: iterateConditions,
        update_id: this.user_details.client_account_no,
      });
      if (data.success) {
        this.isBusy = false;
        this.getJournalList();
      } else {
        this.isBusy = false;
        this.getJournalList();
      }
    },
    /**
     * @description function to validate the length of the file
     * @name validateLength
     * @param value
     * @param size
     * @returns boolean
     */
    goToFreeAPIAccesstokenFetch() {
      this.$router.push({ name: "freeAPI_accesstoken_fetch" });
    },
    /**
     * @description function to validate the length of the file name character
     * @name validateLength
     * @param value
     * @param size
     * @returns boolean
     */
    validateLength(value, size) {
      let bytesLength = value.name;
      bytesLength = bytesLength.length;
      return bytesLength <= size;
    },
    /**
     * @description function get the year and month from a given number
     * @name getDateMonth
     * @param dateInput
     * @returns string 'formatted date string'
     */
    getDateMonth(dateInput) {
      // >2020å¹´8æœˆåº¦
      if (!dateInput) {
        return "";
      }
      return (
        String(dateInput).slice(0, 4) +
        this.$t("YEAR") +
        String(dateInput).slice(4, 6) +
        this.$t("MONTH")
      );
    },
  },
};
</script>

